var all = [];
var isRunning = true;
var find = true;
var faceID;
var level;

const URL = "https://teachablemachine.withgoogle.com/models/UP8COqyl/";


   let model, webcam, labelContainer, maxPredictions;


     // Load the image model and setup the webcam
     function random(){
       return Math.floor(Math.random() * Math.floor(maxPredictions));
     }

     function create_all(prediction){
       for (let i = 0; i < maxPredictions; i++) {
         all.push(prediction[i].className);
       }
     }

     async function init() {
       //document.body.style.overflow = "hidden"
         const modelURL = URL + "model.json";
         const metadataURL = URL + "metadata.json";

         model = await tmImage.load(modelURL, metadataURL);
         maxPredictions = model.getTotalClasses();
         var canvas = document.getElementById("canvas");
         const prediction = await model.predict(canvas);
         create_all(prediction);
         console.log(prediction);
       faceID = random();
        var newDiv = document.getElementById("fin");
      //  newDiv.innerHTML = "<h1>"+all[faceID]+"</h1>";
        let butt = document.getElementById("button");
        butt.style.display = 'none';
        console.log(faceID);
        console.log(all);
         // Convenience function to setup a webcam
         const flip = false; // whether to flip the webcam
         webcam = new tmImage.Webcam(document.body.clientWidth, document.body.clientHeight, flip);
         await webcam.setup(); // request access to the webcam
         await webcam.play();

         window.requestAnimationFrame(loop);



         document.getElementById("webcam-container").appendChild(webcam.canvas);

     }

     async function loop() {
         if(isRunning == true){
         webcam.update();
         predict();
         window.requestAnimationFrame(loop);
       }
     }
    function shot(){
       if(find == true){
      predict();
      }

     }
       async function predict(){
       const prediction = await model.predict(webcam.canvas);
       for (let i = 0; i < maxPredictions; i++) {
         const classPrediction =
               prediction[i].className + ": " + prediction[i].probability.toFixed(2);

          if(prediction[i].probability.toFixed(3) >= 0.988){
            if(prediction[i].className == 'other' || 'people'){
          console.log(prediction[i].className)
            }
          }

       }

   }

   function sucsess(){
     isRunning=false;
      find=false;
      let line = document.getElementById("webcam-container");
      line.style.display = "none";
      let next = document.getElementById('next');
      next.style.display = "block";
   }

   function next(){
     setTimeout(()=>{
      isRunning=true;
      find=true;
        faceID = random();
     var newDiv = document.getElementById("fin");
     newDiv.innerHTML = "<h1>"+all[faceID]+"</h1>";
     let line = document.getElementById("webcam-container");
     line.style.display = "block";
     document.getElementById("webcam-container").appendChild(webcam.canvas);
      window.requestAnimationFrame(loop);
     },3000)
   }

function render() {
  return (
<div>

<button type="button" id="button" onClick={()=>init()}>Start</button>
<div id="fin"></div>
<div id="webcam-container" onClick={()=>shot()} style={{weight:'100vw',height:"100vh"}}/>
<button id="next" style={{display:'none'}} onClick={()=>next()}>Следующий</button>
<canvas id="canvas" />
</div>
  );
}
export default render;
